html {
    max-width: 100vw;
    max-height: 100vh;
    overflow: hidden;
}

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.availability-circle {
    position: relative;
    display: inline-block;
    -ms-flex-item-align: center;
        align-self: center;
    font-size: 0.875rem;
    text-align: center;
}

.availability-circle img {
    position: absolute;
    top: 0.5rem;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    height: 4rem;
    width: auto;
}


